import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../views/Home.vue'
import resume from '../views/vue-resume/xc.vue'
import movie from '../views/vue-movie/Home.vue'
import shopping from '../views/vue-shopping/Shopping.vue'
import clicli from '../views/vue-clicli/Clicli.vue'
import cpple from '../views/vue-cpple/Cpple.vue'
import cover from '../views/vue-cover/Cover.vue'
import blog from '../views/vue-blog/Blog.vue'

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
	{
		path:'/resume',
		name:'resume',
		component:resume
	},
	{
		path:'/movie',
		name:'movie',
		component:movie
	},
	{
		path:'/shopping',
		name:'shopping',
		component:shopping
	},
	{
		path:'/clicli',
		name:'clicli',
		component:clicli
	},
	{
		path:'/cpple',
		name:'cpple',
		component:cpple
	},
	{
		path:'/cover',
		name:'cover',
		component:cover
	},
	{
		path:'/blog',
		name:'blog',
		component:blog
	}
]


const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes
})

export default router
