<template>
	<bag :current="current"></bag>
	<div class="Barrage">
		<div class="btn" @click="txtshow">
			Say sth...
		</div>
		<input type="text" placeholder="留下痕迹吧.贵客! ENTER发射!" id="txt" :class="{txtclass:istxt}" @keyup.enter="txtpush">
	</div>
	<div class="fire" :class="{fireclass:isfire}"><img src="@/assets/Index/fire.png" ></div>
</template>

<script>
import axios from 'axios'
import bag from './bag.vue'

	export default{
		name:'Barrage',
		data() {
			return{
				istxt:false,
				isfire:false,
				message:'',
				current:0,
				
			}
		},
		components:{
			bag,
		},
		methods:{
			txtshow(){
				this.istxt= true
				// console.log(this.istxt)
				this.isfire=false
			},
			txtpush(){
				this.message = document.getElementById("txt").value
				this.send()
				this.istxt= false
				this.isfire=true
			},
			send(){
				axios({
					method:"get",
					url:"http://localhost:8888/insert",
					params:{
						content:this.message
					}
				}).then(r=>{
					console.log(r)
					this.current+= 1
				})
			}
		}
	}
</script>

<style scoped="scoped">
	.Barrage{
		display: flex;
		align-items: center;
		width: 60%;
		height: 6vh;
		position: absolute;
		bottom: 0vh;
		left: 8vw;
	}
	.btn{
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		right: 0vw;
		width: 10%;
		height: 80%;
		border-radius: 25px;
		background:linear-gradient(90deg, #81D8CF,#dcfab8);
		cursor: pointer;
		padding: 2px;
		color: white;
		
	}
	.btn:hover{
		box-shadow:	10px 0 15px #81D8CF;
	}
	#txt{
		position: absolute;
		opacity: 0;
		right: 7vw;
		border-radius: 25px;
		background-color:rgba(255,255,255,.9);
		height: 70%;
	}
	.txtclass{
		animation: show 2s forwards;
	}
	@keyframes show{
		from{
			opacity: 0.6;
			width: 0;
		}
		to{
			opacity: 1;
			width: 60%;
		}
	}
	.fire{
		position: absolute;
		right: 26%;
		bottom: 0;
		transform: translateY(1.5vh);
		width: 2.5rem;
		height: 2.5rem;
	}
	.fire img{
		width: 100%;
	}
	.fireclass{
		animation: shoo 2s forwards ease-in;
	}
	@keyframes shoo{
		0%{
			transform: translateY(1.5vh);
		}
		99%{
			transform: translateY(-100vh);
		}
		99.1%{
			transform: translateY(3vh);
		}
		100%{
			transform: translateY(1.5vh);
		}
	}
</style>
