<template>
	<div id="carsouel" @mouseover="mouseOver()" @mouseout="mouseOut()">
		<div class="img indexZ" v-for="(item, index) in mv" :key="index" :style="[{background:item.bg + 'center'},{backgroundSize:'100%'}]">
			<div class="mtext"><span> <h1>{{item.name}}</h1><h3>{{item.text}}</h3><p>2012年5月5日</p><a href="">Read More</a></span></div>
			<span class="poster"><img :src="item.image" ></span>
		</div>
	</div>
	<div id="imgIndexs">
		<ul>
			<li class="imgIndex" v-for="(item,index) in mv" :key="index"
			@mouseover="indexBtn(index)" :class="{indexBGC:currentIndex === index}">{{item.name}}</li>
		</ul>
	</div>
</template>

<script>

	export default{
		name:'carsouel',
		data(){
			return{
				imgCount:0, //图片个数
				currentIndex:0, //当前图片
				intervalID:'', //停止interval的唯一id,
				mv:[
					{
						name:"IRon Man",
						text:"A sudden and powerful evil force poses a deadly threat to the earth, and no superhero can resist alone.",
						image:"https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel5.165u77exre80.webp",
						bg:"url(https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel5.165u77exre80.webp)",
					},
					{
						name:"Doctor Strange",
						text:"A sudden and powerful evil force poses a deadly threat to the earth, and no superhero can resist alone.",
						image:"https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel1.nm20lj29a74.webp",
						bg:"url(https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel1.nm20lj29a74.webp)"
					},
					{
						name:"Wonder Woman",
						text:"A sudden and powerful evil force poses a deadly threat to the earth, and no superhero can resist alone.",
						image:"https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel2.6v581weg6g40.webp",
						bg:"url(https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel2.6v581weg6g40.webp)"
					},
					{
						name:"Bat Man",
						text:"A sudden and powerful evil force poses a deadly threat to the earth, and no superhero can resist alone.",
						image:"https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel3.5k3u8b3k6zo0.webp",
						bg:"url(https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel3.5k3u8b3k6zo0.webp)"
					},
					{
						name:"Spider Man",
						text:"A sudden and powerful evil force poses a deadly threat to the earth, and no superhero can resist alone.",
						image:"https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel4.5o01w0seaho0.webp",
						bg:"url(https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/marvel4.5o01w0seaho0.webp)"
					},
					]
				}
		},
		mounted(){
		this.imgCount = document.getElementsByTagName("img").length;
		setTimeout(()=>{
		this.startTime();
		})
	},
	methods:{
		/**
		 * 鼠标移动到图片上
		 */
		mouseOver(){
			this.stopTime()
		},
		/**
		 * 鼠标移出图片
		 */
		mouseOut(){
			this.startTime();
		},
		/**
		 * 点击 
		 */
		indexBtn(index){
			this.currentIndex = index
			this.selectImg(index);
		},
		/**
		 * 选中对应的图片设置层级类
		 */
		selectImg(index){
			let imgList = document.getElementsByClassName("img");
			this.clearClass(imgList);
			imgList[index].className = "img indexZ"
		},
		/**
		 * 层级复位
		 */
		clearClass(array){
			for (let i=0 ; i<array.length ; i++){
				array[i].className = "img"
		}
		},
	
		startTime(){
			this.intervalID = window.setInterval(()=>{
			this.currentIndex++;
			if(this.currentIndex > 4){
				this.currentIndex = 0
			}
			this.selectImg(this.currentIndex)
			},2000)
		},
		/**
		 * 鼠标移到图片上时停止图片播放
		 */
		stopTime(){
		window.clearInterval(this.intervalID)
		}
	}
	}
</script>

<style scoped="scoped">
	#carsouel{
		color: white;
		display: flex;
		width: 100%;
		position: relative;
		flex-direction: row;
		height: 60vh;
	}
	.img{
		display: flex;
		position: absolute;
		width: 100%;
		height: 100%;
	}
	
	.indexZ{
		z-index: 2;
		animation: run .5s ease-in-out;
	}
	@keyframes run{
		from{
			opacity: .9;
		}
		to{
			opacity: 1;
		}
	}
	.mtext{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-start;
		box-sizing: border-box;
		width: 100%;
		background-color: rgba(120,120,120,.4);
	}
	.mtext span{
		position: absolute;
		width: 40vw;
		text-align: left;
		left: 10vw;
	}
	.mtext h1{
		font-size: 50px;
	}
	.mtext h3{
		font-size: 20px;
	}
	.mtext span a{
		display: flex;
		justify-content: center;
		align-items: center;
		width: 150px;
		height: 50px;
		background-color: red;
		color: white;
		font-weight: 800;
		text-decoration: none;
		transform: skew(-10deg);
		border-left: 3px solid white;
		font-family: "RobotoCondensed Bold", "Trebuchet MS", Helvetica, Arial, sans-serif;
	}
	.poster{
		display: flex;
		justify-content: center;
		position: absolute;
		width: 30vw;
		height: 100%;
		right: 10vw;
		transform: skewX(-30deg);
		overflow: hidden;
	}
	#carsouel div img{
		width: 40vw;
		height: 100%;
		transform: skewX(30deg);
	}
	#imgIndexs{
		display: flex;
		position: absolute;
		width: 70vw;
		height: 10vh;
		top: 55vh;
		left: 10vw;
		background-color: ghostwhite;
		z-index: 3;
	}
	#imgIndexs ul{
		display: flex;
		position: relative;
		width: 100%;
		height: 100%;
		flex-direction: row;
		list-style: none;
		padding: 0;
		margin: 0;
	}
	#imgIndexs ul li{
		width: 20%;
		height: 70%;
		text-align: left;
		margin-top: 1%;
		padding-left: 10px;
		color: black;
		font-weight: 600;
		font-family: "RobotoCondensed Bold", "Trebuchet MS", Helvetica, Arial, sans-serif;
	}
	.indexBGC{
		background-color: red;
		color: white;
	}
</style>
