<template>
	<div id="contents" @click="addToDetail()">
		<router-link :to="{name:'Detail',query:{movieId:this.movieid}}" class="mv">
		<img v-if="movie.imageSrc" :src="movie.imageSrc" >
		<p>{{movie.filmName}}</p>
		</router-link>
	</div>
</template>

<script>
// import { toRaw } from '@vue/reactivity';
 
 
 
	function getPath(path) {
		return import(`@/${path}`);
	}
	
	export default{
		name:'contents',
		props:{
			movie:{
				type:String
			}
			
		},
		data() {
			return{
				path: '',
				movieid:''
			}
		},
		mounted() {
			this.loadImage();
			this.movieid = this.movie.fid
		},
		watch:{
			movie(val){
				this.movieid = val.fid
			}
		},
		methods:{
			async loadImage() {
				let path = this.movie.ImageSrc || '' ;
				if(path) {
					if(/^(https?:\/\/)([0-9a-z.]+)(:[0-9]+)?([/0-9a-z.]+)?(\?[0-9a-z&=]+)?(#[0-9-a-z]+)?/i.test(this.movie.ImageSrc)) {
						this.path = this.movie.ImageSrc;
					} else {
						let pathSrc = await getPath(this.movie.ImageSrc);
						this.path = pathSrc.default;
					}
				}
			},
			
		}
	}
</script>

<style scoped="scoped">
	#contents{
		display: flex;
		flex-direction: column;
		position: relative;
		width: 18vw;
		height:30vh;
		margin:8vw 1vw;
	}
	.mv{
		width: 15vw;
		height: 50vh;
		background-color: ghostwhite;
		margin: 2vw 2vw;
	}
	.mv img{
		width: 100%;
		height: 100%;
		box-shadow: 0px 5px 5px ghostwhite;
	}
	#contents:hover{
		transform: translateY(-10px);
		color: red;
	}
	a{
		color: ghostwhite;
		font-family: "RobotoCondensed Bold", "Trebuchet MS", Helvetica, Arial, sans-serif;
		font-weight: 1000;
		cursor: pointer;
		text-decoration: none;
	}
</style>
