<template>
	<div class="intro">
		<div class="title">
			<h4>Personal Excerprience</h4><hr/>
		</div>
		<div class="avatar" :class="{'move-item': hasMove}"><img src="https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/xC.6bdq0qoacn00.webp"/></div>
		<div class="introBg" :class="{'move-item':hasMove}"></div>
		<information></information>
	</div>
</template>

<script>
import information from './information.vue'
	export default{
		name:'intro',
		data() {
			return {
				hasMove: false
			}
		},
		components:{
			information,
		},
		mounted() {
			window.addEventListener('scroll', this.scrollPage)
		},
		methods: {
			scrollPage() {
				let scrollTop = window.pageYOffset || document.documentElement.scrollTop;
				this.hasMove = scrollTop > 125;
			}
		}
	}
</script>

<style scoped="scoped">
	.intro{
		position: relative;
		width: 100%;
		height: 330px;
	}
	
	.avatar{
		display: inline-block;
		position: absolute;
		z-index: 100;
	}
	.avatar img{
		width: 160px;
		height: 200px;
		border-radius: 12px 12px 12px 12px ;
		border: 1px solid white;
	} 
	.avatar.move-item {
		position: fixed;
		z-index: 100;
		left: 200px;
		top: 6px;
	}
	.avatar.move-item img {
		width: 48px;
		height: auto;
		z-index: 100;
		transition: all .5s ease-in-out;
	}
	.introBg{
		position: absolute;
		width: 80%;
		height: 200px;
		background-color: rgba(240,120,120,.9);
		z-index: 20;
		border: 1px solid white;
		border-radius: 12px 12px 12px 12px ;
		transform: translateX(70px) translateY(50px);
	}
	
	.move-item {
		opacity: 1.0;
		transition: all .5s linear;
	}
	.move-item.introBg{
		width: 50%;
		height: 200px;
		transform: translateX(50%) translateY(50px);
		transition: all .5s ease-in-out;
	}
	
	
</style>
