<template>
	<div class="contents">
		<router-link :to="this.link">
		<img :src="this.image"/>
		<span >
			<h1>{{title}}</h1>
			<p>{{text}}</p>
		</span>
		</router-link>
	</div>
</template>

<script>
	export default{
		name:'contents',
		props:{
			Datas:String
		},
		data() {
			return{
				title:'',
				text:'',
				image:'',
				link:''
			}
		},
		mounted() {
			this.title = this.Datas.title,
			this.text = this.Datas.text,
			this.image = this.Datas.image
			this.link = this.Datas.link
			// console.log(this.image)
		}
	}
</script>

<style scoped="scoped">
	.contents{
		display: flex;
		width: 80%;
		padding: 6vh;
		background-color: rgba(255,255,255,.8);
		vertical-align: top;
		margin-top: 8vh;
		border-radius: 2vh;
		margin-bottom: 6vh;
		box-shadow: 5px 5px 10px dimgray;
		transition: all 0.6s;
	}
	a{
		color: #242424;
		text-decoration: none;
	}
	.contents:hover{
		transform: translate(-1vw,-1vh);
		box-shadow: 10px 10px 10px #81D8CF;
	}
	.contents span{
		margin-left: 2vw;
	}
	.contents img{
		width: 60%;
		height: 100%;
		float: left;
		margin-right: 2vh;
	}
</style>
