<template>
	<div class="inner">
		<div class="sort">
			<span @click="recommend()" >推荐</span>
			<span @click="score()" > 评分</span>
			<span >语言
				<ul>
					<li @click="chinese()">国语</li>
					<li @click="english()">外语</li>
				</ul>
			</span>
			<span >国家
				<ul>
					<li @click="china()">中国</li>
					<li @click="another()">外国</li>
				</ul>
			</span>
		</div>
		<contents v-for="(item,index) in mv" :key="index" :movie="item" ></contents>
		<div class="pagination">
			<!-- <div class="pag">
				<ul>
					<li v-if="cur>1"><a v-on:click="cur--,pageClick()">上一页</a></li>
					<li v-if="cur==1"><a class="banclick">上一页</a></li>
					<li v-for="index in mv" :key="index"  :class="{ 'active': cur == index}">
					<a v-on:click="btnClick(index)">{{ mv.current }}</a>
					</li>
					<li v-if="cur!=all"><a v-on:click="cur++,pageClick()">下一页</a></li>
					<li v-if="cur == all"><a class="banclick">下一页</a></li>
					<li ><a>共<i>{{all}}</i>页</a></li>
				</ul>
			</div> -->
		</div>
	</div>
</template>

<script>
// import bus from '../detail.js'
import axios from 'axios'
import contents from './contents.vue'
// import pag from './pag.vue'

	export default{
		name:'inner',
		components:{
			contents,
			// pag,
		},
		props:{
			mvsearch:String
		},
		data() {
			return{
				isActive1:true,
				isActive2:false,
				all:'',
				mvUrl:"",
				mv:[],
				mvdetails:[],
				mvParams:{}, //总页数
				cur: 1,//当前页码
				totalPage: 0,//当前条数
				
			}
		},
		mounted() {
			this.recommend()
		},
		watch:{
			mvsearch(val){
				this.mvUrl="http://localhost:8888/search"
				this.mvParams={
					value:val,
					current:1,
					size:12,
				}
				console.log(val)
				this.mvshow()
			}
		},
		methods:{
				mvshow(){
					axios({
						method:"get",
						url:this.mvUrl,
						params:this.mvParams
					}).then(r=>{
						this.mv = r.data.records
						// console.log(r.data)
					})
				},
				recommend(){
					this.mvUrl="http://localhost:8888/recommend"
					this.mvParams={
						n:12
					}
					this.mvshow()
				},
				score(){
					this.mvUrl="http://localhost:8888/search"
					this.mvParams={
								value:"",
								current:1,
								size:12,
								desc:"score",
							}
					this.mvshow()
				},
				languageshow(){
					axios({
						method:"get",
						url:"http://localhost:8888/language/byid",
						params:this.mvParams
					}).then(r=>{
						console.log(r.data)
						this.mv = r.data.relationV
					})
				},
				chinese(){
					this.mvParams={
						id:18
					}
					this.languageshow()
				},
				english(){
					
					this.mvParams={
						id:15
					}
					this.languageshow()
				},
				countyshow(){
					axios({
						method:"get",
						url:"http://localhost:8888/county/byname",
						params:this.mvParams
					}).then(r=>{
						this.mv = r.data.records[0].relationV
						console.log(r.data)
					})
				},
				china(){
					this.mvParams={
								name:"中国",
								current:1,
								size:12,
							}
					this.countyshow()
				},
				another(){
					this.mvParams={
								name:"",
								current:1,
								size:12,
					}
					this.countyshow()
				},
				
				btnClick(data){
					if(data != this.cur){
						this.cur = data
					}
					//根据点击页数请求数据
					this.mvshow(this.cur.toString());
				},
				pageClick(){
					//根据点击页数请求数据
					this.mvshow(this.cur.toString());
				},
				// sendMsg(item){
				// 	bus.$emit('share',item)
				// }
				
		}
	}
</script>

<style scoped="scoped">
	.inner{
		color: white;
		font-weight: 600;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		min-width: 81%;
		padding: 6vw 9vw;
		margin-top: 1vh;
		padding-bottom: 15vw;
		background-color: #272727;
		min-height: 50vh;
	}
	.sort{
		display: flex;
		flex-direction: row;
		position: absolute;
		left: 8vw;
		width: 30vw;
		height: 5vh;
		justify-content: space-around;
	}
	.sort span{
		flex: 1;
		height: 100%;
		display: flex;
		align-items: center;
		flex-direction: column;
		justify-content: flex-start;
		position: relative;
		overflow: hidden;
	}
	.sort span:before{
		content: '';
		position: absolute;
		width: 0;
		bottom: 0;
		height: 5px;
		background-color:red; 
	}
	.sort span:hover:before{
		width: 80%;
		left: 10%;
		transition:all 0.5s;
	}
	.sort span:hover {
		overflow:visible;
	}
	@keyframes ulShow{
		from{
			height: 0;
		}
		to{
			height: 100%;
		}
	}
	.sort ul{
		display: flex;
		flex-direction: column;
		
	}
	.sort ul li{
		display: flex;
		margin-top: 2vh;
		flex-direction: row;
	}
	.sort ul li:hover{
		color: red;
	}
	.active{
		color: red;
	}
	.pagination{
		display: flex;
		position: absolute;
		align-items: center;
		bottom: 0;
		width: 60vw;
		height: 20vh;
	}
	.pag{
		display: flex;
		position: relative;
		justify-content: center;
		flex-direction: row;
		width: 100%;
	}
	.pag ul{
		width: 100%;
		display: flex;
		flex-direction: row;
		justify-content: center;
	}
	ul,li{
	margin: 0px;
	padding: 0px;
	}
	li{
	list-style: none
	}
	.pag li:first-child>a {
	margin-left: 0px
	}
	.pag a{
	border: 1px solid #ddd;
	text-decoration: none;
	position: relative;
	float: left;
	padding: 6px 12px;
	margin-left: -1px;
	line-height: 1.42857143;
	color: ghostwhite;
	cursor: pointer;
	margin-right: 20px;
	}
	.pag a:hover{
	background-color: ghostwhite;
	color:red
	}
	.pag a.banclick{
	cursor:not-allowed;
	}
	.pag .active a{
	color: #fff;
	cursor: default;
	background-color: #E96463;
	border-color: #E96463;
	}
	.pag i{
	font-style:normal;
	color: #d44950;
	margin: 0px 4px;
	font-size: 12px;
	}
</style>
