<template>
	<div class="progress" :class="{progress_playing: isPlaying}">
		<h2 class="progress_title">{{ name }}</h2>
		<p class="progress_text">
			{{formatTime}} / {{formatDuring}}
		</p>
		<div class="progress_line">
			<span :style="{width: progress}"></span>
		</div>
	</div>
</template>

<script>
import {mapState} from 'vuex'
import {player} from '@/player.js'
	export default{
		name:'ProgressBar',
		data() {
			return{
				name:'',
				position: 0,
				duration: 0.1,
				progress:''
			}
		},
		computed:{
			...mapState(['isPlaying']),
			
			formatTime () {
			  const min = Math.floor(this.position / 60)
			  const sec = Math.floor(this.position % 60)
			  return `${min}:${sec < 10 ? '0' : ''}${sec}`
			},
			formatDuring () {
			  const min = Math.floor(this.duration / 60)
			  const sec = Math.floor(this.duration % 60)
			  return `${min}:${sec < 10 ? '0' : ''}${sec}`
			},
			formatName(val){
				return val.replace(/\.mp3$/, '')
			}
		
		},
		mounted() {
			const draw = () =>{
				requestAnimationFrame(draw)
				const progress = player.position/ player.duration
				this.progress = `${(progress * 100).toFixed(2)}%`
				this.position = player.position
				this.duration = player.duration
				this.name = player.current.file ? player.current.file.name:''
			}
			draw()
		}
	}
</script>

<style scoped="scoped">
.progress {
	position: absolute;
	display: flex;
	flex-direction: column;
	align-items: flex-end;
	width: 100%;
  height: 100%;
  border-radius: 6px;
  background-color: rgba(255, 255, 255, 0.5);
  transition: all 0.6s ease;
	padding-right: 5px;
}
.progress_playing {
  transform: translateX(100%);
}
.progress_title {
  padding-top: 0;
  font-size: 12px;
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
	margin: 0;
	padding-bottom: 2px;
}
.progress_text {
  font-size: 12px;
  font-weight: bold;
	margin-right: 0;
  color: #CCC;
  transform: scale(0.8);
  transform-origin: left top;
	margin: 0;
}
.progress_line {
  height: 3px;
	width: 70%;
  border-radius: 3px;
  overflow: hidden;
  background-color: #ddd;
}
.progress_line span {
  display: block;
  height: 100%;
  background-color: #ec51a5;
}
</style>
