<template>
	<div class="momments" >
		<img :src="this.image" >
		<span>
			<h1>{{title}}</h1>
			<p>{{text}}</p>
		</span>
	</div>
</template>

<script scoped="scoped">
	export default{
		name:'momments',
		props:{
			Datas:String
		},
		data() {
			return{
				title:'',
				text:'',
				image:'',
			}
		},
		mounted() {
			this.title = this.Datas.title,
			this.text = this.Datas.text,
			this.image = this.Datas.image
		}
	}
</script>

<style scoped="scoped">
	.momments{
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 50%;
		height: 80%;
		padding:3vh 4vw;
		background-color: rgba(255,255,255,.8);
		margin-top: 8vh;
		margin-left: 4vw;
		border-radius: 20%;
		margin-bottom: 6vh;
		box-shadow: 5px 5px 10px dimgray;
		transition: all 0.5s; 
	}
	.momments:hover{
		transform: translate(-1vw,-1vh);
		box-shadow:10px 10px 10px  #91d8a5;
	}
	
	.momments:nth-child(2n):hover{
		transform: translate(-1vh,19vh);
		box-shadow:10px 10px 10px  #91d8a5;
	}
	.momments img{
		width: 100%;
		margin: 0;
	}
</style>
