<template>
	<div class="contents">
		<div class="picture">
			<img :src="this.image"/>
		</div>
		<span >
			<h5>{{title}}</h5>
		</span>
	</div>
</template>

<script>
	export default{
		name:'contents',
		props:{
			Datas:String
		},
		data() {
			return{
				title:'',
				image:''
			}
		},
		mounted() {
			this.title = this.Datas.title,
			this.image = this.Datas.image
			// console.log(this.image)
		}
	}
</script>

<style scoped="scoped">
	.contents{
		display: flex;
		width: 16vw;
		padding: 0;
		margin: 3vw 3vh;
		flex-direction: column;
	}
	.picture{
		width: 100%;
		border-radius: 2vh;
		box-shadow: 5px 5px 10px dimgray;
		transition: all 0.6s;
		overflow: hidden;
	}
	.picture:hover{
		transform: translate(-1vw,-1vh);
		box-shadow: 10px 10px 10px coral;
	}
	.picture img{
		width: 100%;
		height: 100%;
	}
</style>
