<template>
	<div class="calender">
		<div class="topDate">
				{{year}}年{{month}}月
		</div>
		<div class="btnWrap">
			<ul>
				<li @click="nextBtn">前瞻</li>
				<li @click="todayBtn">当下</li>
				<li @click="lastBtn">回瞰</li>
			</ul>
		</div>
		<div class="dateWrap">
			<div class="plan">
				<p>工作安排：</p>
				<ul class="workP" >
					<li v-for="(item,index) in workP" :key="index">{{item}}</li>
				</ul>
				<p>锻炼安排：</p>
				<ul class="trainP">
					<li v-for="(item, index) in trainP" :key="index" :class="{'now-week': nowWeek === item}">{{item}}</li>
				</ul>
				
				<p>周期：</p>
				<ul class="week">
					<li v-for="(item,index) in week" :key="index">{{item}}</li>
				</ul>
			</div>
			<ul class="day">
				<li v-for="(item,index) in days" :key="index" :class="{'now-day': nowDay === item }">{{item}}</li>
			</ul>
		</div>
	</div>
</template>

<script>
	export default{
		name:'Calender',
		data() {
			return{
				year:'',
				month:'',
				days:[],
				nowWeek: 0,
				nowDay: 1,
				week:["一","二","三","四","五","六","日"],
				trainP:["胸臂","背肩","腹腿","胸臂","背肩","腹腿","休息"],
				workP:["计划","学习","实行","完善","总结","回顾","思考"]
			}
		},
		methods:{
			getDays(Y,M){
				let day = new Date(Y, M, 0).getDate()
				return day;
			},
			getWeek(){
				let now = new Date()
				now.setFullYear(this.year)
				now.setMonth(this.month-1)
				now.setDate(0);
				let week = now.getDay();
				return week;
				
			},
			pushDays(){
				for(let i=1; i<this.getDays(this.year,this.month)+1;i++){
					this.days.push(i)
				}
				for(let i=1; i<36-this.getDays(this.year,this.month)-this.getWeek(this.year,this.month);i++){
					this.days.push(i)
				}
				for(let i=0; i<this.getWeek(this.year,this.month);i++){
					var lastMonthDays=this.getDays(this.year,this.month-1)
					this.days.unshift(lastMonthDays-i)
				}
			},
			getDate(){
				let now = new Date();
				this.year=now.getFullYear();
				this.month=now.getMonth()+1;
				this.nowDay = now.getDate();
				this.nowWeek = now.getDay()
				this.pushDays();
				console.log(this.nowWeek)
			},
				
			nextBtn(){
				this.days=[];
				if(this.month<12){
					this.month=this.month+1;
					this.pushDays();
				}else{
					this.month=this.month=1;
					this.year=this.year+1;
					this.pushDays();
				}
			},
			todayBtn(){
				this.days=[];
				let now=new Date();
				this.year=now.getFullYear();
				this.month=now.getMonth()+1;
				this.pushDays();
			},
			lastBtn(){
				this.days=[];
				if(this.month>1){
					this.month=this.month-1;
					this.pushDays();
				}else if(this.year>1970){
					this.month=12;
					this.year=this.year-1;
					this.pushDays();
				}else{
					alert("太远了，很抱歉时光机也无能为力。")
				}
			},
			// showDate(){
			// 	let now = new Date()
			// 	this.nowLi=now.getFullYear().toString()+(now.getMonth()+1).toString()+now.getDate().toString()
			// },
		},
		mounted() {
			this.getDate();
			
		}
	}
</script>

