<template>
	<div id="contain">
		<div class="timelineMarker"></div>
		<div class="timelineItem">
			<div class="timelineInfo">
				<p class="date">{{date}}</p>
				<span class="udline"></span>
			</div>
			<div class="timelinecontent">
				<h3>{{title}}</h3>
				<p>{{text}}</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		name:'contain',
		props:{
			Datas:String
		},
		data() {
			return{
				title:'',
				text:'',
				date:''
			}
		},
		mounted() {
			this.title = this.Datas.title,
			this.text = this.Datas.text,
			this.date = this.Datas.date
		}
	}
</script>

<style scoped="scoped">
	#contain{
		display: flex;
		position: relative;
		width: 100%;
		margin-bottom: 6vh;
		margin-top: 6vh;
		justify-content: flex-start;
	}
	#contain:nth-child(2n){
		justify-content: flex-end;
	}
	.timelineItem{
		position: relative;
		width: 40%;
		background-color: rgba(255,255,255,.8);
		box-shadow: 5px 5px 10px dimgray;
		padding:10px 30px;
		float: left;
		z-index: 99;
		transition: all 0.6s;
	}
	.timelineItem:hover{
		transform: translate(-1vw,-1vh);
		box-shadow:10px 10px 10px  #dcfab8;
	}
	.timelineInfo{
		position: absolute;
		right: 0;
		top: 0;
		padding:10px;
	}
	.date{
		position: relative;
		color: #230547;
		font-weight: 800;
		/* text-shadow:0px 0px 10px white; */
		z-index: 10;
		margin: 0;
	}
	.udline{
		position: absolute;
		content: '';
		width: 80%;
		height: 10px;
		background-color: #99CCCC;
		right: 10px;
		top: 24px;
		z-index: 0;
	}
	.timelineMarker{
		content: "";
		width: 20px;
		height: 20px;
		background-color:#81D8CF;
		box-shadow: 2px 2px 5px gray;
		border-radius: 50%;
		position: absolute;
		left: 49%;
		top: 0;
		z-index: 10;
	}
	.v-enter-active, .v-leave-active {
	    transition: all 0.3s ease;
	}
	.v-enter{
		transform: translateX(-200%);
	}
	.v-enter-to{
		transform: translateX(-100%);
	}
	.v-leave{
		transform: translateX(0);
	}
	.v-leave-to{
		transform: translateX(100%);
	}

</style>
