import Vue from 'vue'
import { createStore } from 'vuex'

const COVER_URL = [
	"https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/PlayerCover1.7cpuryxfock0.webp",
	"https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/PlayerCover2.3ev48o57nxc0.webp",
	"https://cdn.jsdelivr.net/gh/xCBot/WebImages@master/PlayerCover3.3aepx1gf08g0.webp"
]

export default createStore({
	state: {
	  isPlaying: false,
	  coverUrl: '',
		Music_URL:[
	"require('./assets/Index/musci-cmsj.mp3'",
]
	},
	getter:{
		
	},
	mutations:{
		togglePlay (state, toggle) {
		  state.isPlaying = toggle !== undefined ? toggle : !state.isPlaying
		},
		changeCover (state) {
		  while (1) {
				const index = Math.floor(Math.random() * 3)
				const coverUrl = COVER_URL[index]
				if (coverUrl !== state.coverUrl) {
					state.coverUrl = coverUrl
					break
				}
			}
		}
	},
	actions:{}
})