<template>
	<div class="music" >
		<ul class="musicList" :class="{musicCss:ismusic}" @mouseleave="musicListHide">
			<li v-for="item,index in musicItem" :key="index" @click="musicPlay(item.id,index)">{{item.name}}</li>
		</ul>
		<input type="text" placeholder="陈奕迅" v-model="musicName"/>
		<audio :src='this.musicUrl' controls="controls" @mouseover="musicListShow" ></audio>
	</div>
</template>

<script >
import axios from 'axios'
	export default{
		name:'music',
		data() {
			return{
				musicItem:[],
				id:[],
				musicUrl:"",
				ismusic:false,
				musicName:'',
				searchName:'陈奕迅'
			}
		}, 
		watch:{
			id(id1,id2){
					this.musicUrl = "https://music.163.com/song/media/outer/url?id="+id1+".mp3"
			},
			musicName(name1){
				this.searchName = name1
				this.musicget()
				this.ismusic = true
			}
			
		},
		mounted() {
			this.musicget()
		},
		methods:{
			musicget(){
				axios({
					method:"get",
					url:"https://autumnfish.cn/search?keywords=" + this.searchName,
				}).then(r=>{
					this.musicItem = r.data.result.songs
					console.log(r)
				})
			},
			musicListShow(){
				this.ismusic = true
			},
			musicListHide(){
				this.ismusic = false
			},
			musicPlay(id,index){
				this.id = id
			}
		},
	}
</script>

<style>
	.music{
		display: flex;
		flex-direction: column;
		position: absolute;
		right: 0;
		bottom: 0;
		width: 100px;
		font-weight: 800;
		color: #242424;
		font-family: "微软雅黑";
	}
	.music audio{
		width:100%;
		height: 3vh;
	}
	.music input{
		border-radius: 10px;
		padding-left: 10px;
	}
	.musicList{
		position: relative;
		display: flex;
		flex-direction: column;
		width: 100%;
		height: 0;
		overflow: auto;
		padding: 0;
		list-style: none;
		margin: 0;
		z-index: 9999;
		background-color: rgba(255,255,255,.3);
		border-radius: 10%;
	}
	.musicList::-webkit-scrollbar{
		display:none;
	}
	.musicList li{
		margin-left: 1vw;
		line-height: 30px;
	}
	.musicCss{
		animation: musicShow 1s forwards;
	}
	@keyframes musicShow{
		from{
			opacity: 0.6;
			height: 0;
		}
		to{
			opacity: 0.9;
			height: 300px;
		}
	}
</style>
